import { makeAutoObservable } from 'mobx';
import { TokenState, LPTokenState } from './TokenState';
import { PoolState } from './PoolState';
import { ContractState, AeolusContractState } from './ContractState';
import { NetworkState } from './NetworkState';
import { BigNumberState } from '../type';
import { rootStore } from '../index';
import { _ } from '../../lib/lodash';

export class ChainState {
  name: string;
  path: string;
  network: NetworkState;
  chainId: number;
  rpcUrl: string;
  gnosisRpcUrl: string;
  baseRpcUrl: string;
  explorerURL: string;
  explorerName: string;
  swapName: string;
  swapURL: string;
  APIURL: string;
  relayer: string;
  L1FinalizationRate: number;
  L2FinalizationRate: number;
  userRequestTopic: string;
  AffirmationCompletedTopic: string;
  L2UserRequestTopic: string;
  pools: {
    [key: string]: PoolState;
  };
  metas: {
    rewardPerBlock?: number;
    [key: string]: any;
  } = {};
  Aeolus: AeolusContractState;
  AeolusV2: AeolusContractState;
  UniswapV2CycloneRouter: ContractState;
  MultiCall: ContractState;
  LPToken: LPTokenState;
  LPTokenV2: LPTokenState;
  CYCToken: TokenState;
  XRCToken: TokenState;
  L1Helper: ContractState;
  HorizonPool: ContractState;
  HomeAMB: ContractState;
  AMBBridgeHelper: ContractState;
  Coin: TokenState;
  analyicsClient: any;
  sort = {
    apy: 'desc',
    tvl: null
  };

  constructor(args: Partial<ChainState>) {
    Object.assign(this, args);
    makeAutoObservable(this, { network: false });
  }

  getPool(key: string): PoolState {
    return this.pools[key];
  }

  get poolList() {
    let pools = Object.values(this.pools)

    _.each(this.sort, (v, k) => {
      if (!v) return;
      pools = pools.sort((a, b) => (v == 'desc' ? Number(a.order) - Number(b.order) : Number(b.order) - Number(a.order)));
    });

    return pools;
  }

  setSort(key: string) {
    const _val = this.sort[key];
    //@ts-ignore
    this.sort = {};
    this.sort[key] = _val == 'desc' ? 'asc' : 'desc';
  }
}
