import { makeAutoObservable } from 'mobx';
import TimeAgo from 'javascript-time-ago';
import en from 'javascript-time-ago/locale/en';
import zh from 'javascript-time-ago/locale/zh';
import ru from 'javascript-time-ago/locale/ru';
import { rootStore } from './index';
import { analyticsETHClient } from '../lib/analytics-bsc-gql';
import { eventBus } from '../lib/event';
import { marketUtils } from '../utils/market';
import { isMobile } from "react-device-detect";
import BigNumber from 'bignumber.js';
import { BigNumberState } from '../store/type';

TimeAgo.addDefaultLocale(en);
TimeAgo.addLocale(ru);
TimeAgo.addLocale(zh);

export class BaseStore {
  constructor() {
    makeAutoObservable(this);
    // console.log('BaseStore');
    this.startRefetch();
  }

  timer: any;

  downloadShieldedKey = (address, priavetKey) => {
    if( isMobile )
      return;
      
    const element = document.createElement("a");
    const file = new Blob([priavetKey], {
      type: "text/plain"
    });
    element.href = URL.createObjectURL(file);
    element.download = 'Horizon Shielded Key - ' + address.substring(0, 5) + '.txt';
    document.body.appendChild(element);
    element.click();
  };

  startRefetch() {
    if (this.timer) clearInterval(this.timer);
    this.timer = setInterval(() => {
      eventBus.emit('amount.refetch');
      this.refetchTimer += 1;
    }, 15000);
  }

  startRefetchForce() {
    eventBus.emit('amount.refetch');
    this.refetchTimer += 1;
    this.startRefetch();
  }

  tempIndex = Number(window.localStorage.getItem('tempIndex'));
  timeAgos = {
    en: new TimeAgo('en-US'),
    ru: new TimeAgo('ru-RU'),
    zh_CN: new TimeAgo('zh-CN')
  };
  get timeAgo() {
    const timeAgo = this.timeAgos[rootStore.lang.lang];
    return timeAgo || this.timeAgos.en;
  }
  ip = '';
  geo = '';
  apy = 0;
  refetchTimer = 0;
  cycPrice = 0;
  ethPrice = 0;
  coinPrice = [];
  m87PriceETH = new BigNumber('0');
  m87PriceUSD = new BigNumberState({ value: new BigNumber(0) });
  tvl = 0;

  loading = {
    visible: false,
    msg: '',
    showConfirm: true,
    showCancel: false,
    showLoading: false,
    confirmText: ''
  };

  pendingPool = {
    visible: false,
  };

  modal = {
    visible: false,
    title: '',
    msg: ''
  };
  setModal(args: Partial<BaseStore['modal']>) {
    Object.assign(this.modal, { visible: true }, args);
  }
  closeModal() {
    this.modal = {
      visible: false,
      title: '',
      msg: ''
    };
  }

  setPendingPool() {
    Object.assign(this.pendingPool, { visible: true });
  }
  closePendingPool() {
    this.pendingPool = {
      visible: false,
    };
  }

  closeLoading() {
    this.loading = {
      visible: false,
      msg: '',
      showConfirm: true,
      showCancel: false,
      showLoading: false,
      confirmText: ''
    };
  }

  setLoading(args: Partial<BaseStore['loading']>) {
    Object.assign(this.loading, { visible: true, showLoading: false, showConfirm: true }, args);
  }

  async getCycPrice() {
    const price = await analyticsETHClient.chain.query.price({ symbol: 'ETH' }).execute({ usd: 1 });
    const priceBNB = await analyticsETHClient.chain.query.price({ symbol: 'BNB' }).execute({ usd: 1 });
    const priceMatic = await analyticsETHClient.chain.query.price({ symbol: 'MATIC' }).execute({ usd: 1 });
    this.coinPrice['ETH'] = price.usd;
    this.coinPrice['BNB'] = priceBNB.usd;
    this.coinPrice['MATIC'] = priceMatic.usd;
    this.ethPrice = price.usd;
    this.m87PriceETH = await rootStore.god.getM87PriceETH();
    // console.log('this.m87PriceETH', this.m87PriceETH.toString());
    this.m87PriceUSD.fixed = 9;
    this.m87PriceUSD.setValue(this.m87PriceETH.multipliedBy(this.ethPrice));
  }
  init() {
    this.getCycPrice();
  }
}