import 'globalthis/auto';
import 'react-app-polyfill/stable';
import React, { useEffect } from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import { observer, useLocalStore } from 'mobx-react-lite';
import './App.css';
import * as serviceWorker from './serviceWorker';
import { BrowserRouter as Router, Switch, Route, Redirect } from 'react-router-dom';
import { useStore } from './store';
import { getLibrary } from './lib/web3-react-bsc';

import * as Sentry from '@sentry/react';
import { Integrations } from '@sentry/tracing';
import { Web3ReactProvider } from '@web3-react/core';

import { Leva, useControls } from 'leva';
import { Mainnet } from './pages/Home/Mainnet';
import { Bscnet } from './pages/Home/Bscnet';
import { Polygon } from './pages/Home/Polygon';
import { Modal } from './components/Modal';
import ModalView from './components/ModalView';
import Loading from './components/Loading';

if (process.env.NODE_ENV == 'production') {
  Sentry.init({
    dsn: 'https://3731f73d7eaf4e008d0409f867665f99@o563381.ingest.sentry.io/5703305',
    integrations: [new Integrations.BrowserTracing()],
    tracesSampleRate: 1.0
  });
}
const App = observer(() => {
  const { lang, base, dev, god } = useStore();
  const setting = useControls({ devUI: dev.devUI, devPool: dev.devPool });

  const store = useLocalStore(() => ({
    showLoadingView: false,
    loadingText: ''
  }));
  useEffect(() => {
    lang.init();
    base.init();
  }, []);
  useEffect(() => {
    Object.assign(dev, setting);
  }, [setting]);

  useEffect(() => {
    store.showLoadingView = god.showLoadingView;
  }, [god.showLoadingView]);

  useEffect(() => {
    store.loadingText = god.loadingText;
  }, [god.loadingText]);

  return (
    <React.StrictMode>
      <Leva hidden={!dev.dev} />
      <Modal />
      {
        store.showLoadingView &&
        <ModalView>
          { window.scrollTo(0, 0) }
          <Loading text={store.loadingText} />
        </ModalView>
      }
      <Router>
        <Switch>
          {/* <Route exact path="/eth">
            <Web3ReactProvider getLibrary={getLibrary}>
              <Mainnet />
            </Web3ReactProvider>
          </Route> */}
          <Route exact path="/eth">
            <Web3ReactProvider getLibrary={getLibrary}>
              <Mainnet />
            </Web3ReactProvider>
          </Route>
          <Route exact path="/bsc">
            <Web3ReactProvider getLibrary={getLibrary}>
              <Bscnet />
            </Web3ReactProvider>
          </Route>
          {/* <Route exact path="/polygon">
            <Web3ReactProvider getLibrary={getLibrary}>
              <Polygon />
            </Web3ReactProvider>
          </Route> */}
          
          <Redirect to='/eth' /> 
        </Switch>
      </Router>
    </React.StrictMode>
  );
});

ReactDOM.render(<App />, document.getElementById('root'));

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
